import React from 'react';
import { Container, Image, Menu, Icon } from 'semantic-ui-react';

export default() => (
  <Menu>
    <Container fluid style={{height: '100px', background: '#DAAD86', padding:'2px'}}>
      <Menu.Item as='a' header>
        <Icon name='ethereum' style={{width:"100px"}} size="huge"/>
      </Menu.Item>

      <Menu.Menu position='right'>
        <Menu.Item as='a' name='login'>
          Login
        </Menu.Item>

        <Menu.Item as='a' name='regiseter'>
          Register
        </Menu.Item>
      </Menu.Menu>
    </Container>
  </Menu>
);
